<template>
  <div class="header-menu">
      <div class="header-menu__logo">
          <img src="@/assets/logo.png" alt="" class="header-menu__logo-img">
      </div>
      
      <div class="header-menu__items">
          <div class="header-menu__items-item" v-for="(item, ind) of menuItems" :key="item" @mouseenter ="showEl(ind)" @mouseout="hideEl(ind)">{{item.name}}</div>
      </div>
      <div class="header-menu__phone">+7 (831) 592 73 52</div>
      
      <div class="header-menu__items">
          <div class="header-menu__items-item" v-for="elements of menuItems" :key="elements" >
              <div class="element" v-for="element of elements.items" :key="element" v-show="elements.show" @mouseenter ="showEl(index)" @mouseout="hideEl(index)">{{element}}</div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'HeaderMenu',
  data () {
    return {
        menuItems: [
            {
                name: 'О компании',
                items: ['string1','string2','string3'],
                show: false
            },
            {
                name: 'Продукция',
                items: ['string11','string2','string3'],
                show: false
            },
            {
                name: 'Культуры',
                items: ['string12','string2','string3'],
                show: false
            },
            {
                name: 'Статьи',
                items: ['string13','string2','string3'],
                show: false
            },
            {
                name: 'Документация',
                items: ['string14','string2','string3'],
                show: false
            },
            {
                name: 'Контакты',
                items: ['string15','string2','string3'],
                show: false
            }
        ],
        index: 0
    }
  },
  methods:{
      showEl(value){
          this.index = value
          this.menuItems[value].show = true;
      },
      hideEl(value){
          this.index = value
          this.menuItems[value].show = false;
      }
  }
}
</script>


<style>
    .header-menu{
        position: relative;
        margin-top: 75px;     
        width: 90%;
        height: 50px;
        text-align: center;
        left: 0;
        right: 0;
        margin: auto;
    }
    .header-menu__logo{
        position: relative;
        width: 25%;
    }
    .header-menu__logo-img{
        float: left;
        width: 240px;
        height: 40px;
    }
    .header-menu__items{
        position: relative;
        display: inline-block;
        width: 59%;
        height: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
    }
    .header-menu__items-item{
        display: inline-block;
        width: 16.66%;
        color: #4f4f51;
        font-size: 20px;
    }
    .header-menu__phone{
        position: relative;
        display: inline-block;
        font-size: 20px;
        color: #4e4f51;
        text-align: right;
        width: 15%;
        right: 0;
    }
</style>


