<template>
  <div class="spinner">
    <div class="spinner__img"> <img class="spinner__img" :src="data[num].img" alt=""></div>
    <div class="spinner__discription">    </div>
    <div class="spinner__discription-info">
            <div class="spinner__discription-info-firstline">{{data[num].title}}</div>  
            <div class="spinner__discription-info-secondline">{{data[num].name}}</div>
            <div class="spinner__discription-info-thirdline" v-for="item of data[num].discription" :key="item"> <div class="square"></div>{{item}}</div>
        </div>
    <div class="spinner__marker">
        <div class="spinner__marker-item" v-for="(circle, index) of 3" :key="circle" @click="changeSlide(index)"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Spinner',
  components: {
  },
  data () {
    return {
        data: [
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    'Повышение полевой всхожести на 10-15%',
                    'Активизация иммунитета и стрессоустойчивости расстений'
                ],
                img: '/static/slider/1-5.png'
            },
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    'Повышение полевой всхожести на 10-15%',
                    'another one'
                ],
                img: '/static/slider/1-2.png'
            },
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    'Повышение полевой всхожести на 10-15%',
                    'anoter two'
                ],
                img: '/static/slider/1-4.png'
            },
        ],
        num: 0
    }
  },
  mounted(){
      
      setInterval(this.rotateSlide, 5000)
  },
  methods:{
      changeSlide(val){
          this.num = val
      },
      rotateSlide(){
          this.num +=1
          if (this.num === 3){
              this.num = 0
          }
      }
  }
}
</script>


<style>
  .spinner{
      width: 100%;
      margin-top: 30px;
      margin:  0 0 30px 0;
  }
  .spinner__img{
      width: 100%;
      background: grey;
      height: 715px;;
  }
  .spinner__marker-item{
      width: 10px;
      height: 10px;
      border: 2px solid #c2bcbc;
      background: white;
      border-radius: 50%;
      margin: 10px;
      display: inline-block;
  }
  .spinner__marker-item:hover{
      cursor: pointer;
  }
  .spinner__discription{
      position: absolute;
      right: 0;
      width: 40%;
      height: 675px;
      margin-top: -715px;
      background-color: rgba(255, 255, 255, 0.1);
      line-height: 1; 
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);  
      max-width: 50%;
      max-height: 50%;
      padding: 20px 40px; 
  }
  .spinner__discription-info{
      opacity: 1;
      position: absolute;
      right: 0;
      width: 40%;
      height: 715px;
      margin-top: -715px; 
      color: white;
  }
  .spinner__discription-info-firstline{
      text-align: left;
      margin-left: 60px;
      font-size: 30px;
  }
  .spinner__discription-info-secondline{
      text-align: left;
      margin: 45px 0 60px 60px;
      font-size: 60px;
  }
  .spinner__discription-info-thirdline{
      text-align: left;
      margin-left: 60px;
      font-size: 15px;
  }
  .square{
      width: 10px;
      height: 10px;
      margin: 0 15px;
      background: white;
      display: inline-block;
  }
</style>


